.App {
	position: absolute;
	top: 0;
	left: 0;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;

	width: 100vw;
	height: 100vh;
}

.example-enter {
	margin-top: 1.875rem;

	opacity: 0.01;
}

.example-enter.example-enter-active {
	margin-top: 0;

	-webkit-transition: opacity 0.5s ease, margin 0.5s ease;

	-o-transition: opacity 0.5s ease, margin 0.5s ease;

	transition: opacity 0.5s ease, margin 0.5s ease;

	opacity: 1;
}

.example-leave {
	margin-top: 0;

	opacity: 1;
}

.example-leave.example-leave-active {
	margin-top: -1.875rem;

	-webkit-transition: opacity 0.3s ease, margin 0.5s ease;

	-o-transition: opacity 0.3s ease, margin 0.5s ease;

	transition: opacity 0.3s ease, margin 0.5s ease;

	opacity: 0.01;
}



